html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  overflow-x: hidden;
  margin: 0;
  color: #282828;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  background-color: #a995d1; }

h1, h2, h3 {
  font-family: 'Inconsolata', monospace, 'Tahoma', sans-serif;
  font-weight: 400; }

h2 {
  font-size: 1.8em; }

a {
  color: #71a7b3;
  text-decoration: none;
  -webkit-transition: color 0.25s;
  transition: color 0.25s; }
  a svg.icon {
    height: 25px;
    width: 25px; }
    a svg.icon .background {
      -webkit-transition: fill 0.25s;
      transition: fill 0.25s;
      fill: #71a7b3; }

a:hover {
  color: #aad060; }
  a:hover svg.icon .background {
    fill: #aad060; }

.wrapper {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 40px; }
  @media only screen and (max-width: 910px) {
    .wrapper {
      padding: 0; } }

.HomeComponent {
  background-color: white;
  padding: 40px;
  max-width: 900px;
  box-shadow: rgba(0, 0, 0, 0.3) 4px 7px 93px 3px; }
  .HomeComponent .top {
    display: flex;
    align-items: center; }
    .HomeComponent .top .NameCardComponent {
      margin-left: 40px; }
  .HomeComponent .columns {
    display: flex; }
    .HomeComponent .columns .leftColumn {
      margin-top: 40px;
      max-width: 250px;
      min-width: 250px; }
    .HomeComponent .columns .rightColumn {
      margin-left: 40px;
      margin-top: 40px; }
  @media only screen and (max-width: 910px) {
    .HomeComponent .top {
      flex-direction: column;
      align-items: initial; }
      .HomeComponent .top .NameCardComponent {
        margin: 0; }
    .HomeComponent .columns {
      flex-direction: column; }
      .HomeComponent .columns .leftColumn {
        margin: 0; }
      .HomeComponent .columns .rightColumn {
        margin: 0; }
        .HomeComponent .columns .rightColumn .AProjectComponent .projectIcon {
          display: none; } }

